import React, { useState } from 'react';
import GoogleMapReact from 'google-map-react';
import Marker from '../Marker/Marker';
import './Map.scss';

const AnyReactComponent = () => <div></div>;

const SimpleMap = (props) => {
    const [center] = useState({lat: 30.601546, lng: 32.274464 });
    const [zoom] = useState(15);
    return (
        <div className='map-box'>
        <GoogleMapReact
          bootstrapURLKeys={{ key: 'AIzaSyAVeN74oryYF28c0h99bPi0RpiuRzWx-rA' }}
          defaultCenter={center}
          defaultZoom={zoom}
        >
          <Marker
            lat={30.605639}
            lng={32.273511}
            name="My Marker"
            color="blue"
          />

          <Marker
            lat={30.595900}
            lng={32.275510}
            name="My Marker"
            color="blue"
          />

          <AnyReactComponent
            lat={14.0168}
            lng={76.9558}
            text="My Marker"
          />
        </GoogleMapReact>
      </div>
    );
}

export default SimpleMap;