import React, { Component } from 'react';
import './Navbar.scss';
import SearchBar from './../SearchBar';
import MainLogo from './../MainLogo';
import BasketIcon from './../BasketIcon';
import MenuIcon from './../MenuIcon';
import { Container, Row, Col } from 'react-bootstrap';

class Navbar extends Component {
  componentDidMount(){
    let nav = document.getElementsByClassName('nav-container')[0];
    let image = document.getElementsByClassName('open-show-container')[0];
    console.log(`${image}`);
    window.addEventListener(`scroll`,()=>{
      if(window.scrollY>image.offsetHeight){
        nav.style.backgroundColor=`#4d4d4dff`;
        console.log(window.scrollY);
      }
      else{
        nav.style.backgroundColor=`#4d4d4d00`;
      }
    });
  };
  render(){
    return (
      <nav>
        <Container fluid className='nav-container'>
          <Row>
            <Col xl={2} lg={2} md={2} sm={0} xs={0}>
              <SearchBar/>
            </Col>
            <Col xl={8} lg={8} md={8} sm={10} xs={10}>
              <MainLogo/>
            </Col>
            <Col xl={1} lg={1} md={1} sm={1} xs={1}>
              <BasketIcon/>
            </Col>
            <Col xl={1} lg={1} md={1} sm={1} xs={1}>
              <MenuIcon/>
            </Col>
          </Row>
        </Container>
      </nav>
    );
  };
};

export default Navbar;
